@use "../global/variables.scss" as v;

.add-product {
  padding: 13px 0 18px;

  &:first-child {
    padding-top: 12px;
  }

  @media (min-width: v.$tablet-width) {
    padding: 26px 37px 27px;

    &:first-child {
      padding-top: 25px;
    }
  }

  @media (min-width: v.$desktop-width) {
    display: flex;
    align-items: center;
    gap: 40px;
    padding: 15px 0 16px;

    &:first-child {
      padding-top: 15px;
    }

    &:last-child {
      padding-top: 15px;
    }
  }
}

.add-product__content-wrapper {
  @media (min-width: v.$tablet-width) {
    display: flex;
    flex-grow: 1;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 22px;
  }

  @media (min-width: v.$desktop-width) {
    margin-bottom: 0;
  }
}

.add-product__caption {
  margin: 0;
  margin-bottom: 13px;
  font-size: 16px;
  font-weight: normal;
  line-height: 16px;
  text-transform: uppercase;

  @media (min-width: v.$tablet-width) {
    flex-grow: 1;
    margin-bottom: 0;
    font-size: 20px;
    line-height: 20px;
  }
}

.add-product__description {
  width: 100%;
  font-family: v.$second-font;
  line-height: 14px;
  color: v.$main-text-color;
  margin-bottom: 15px;
  border-spacing: 0;

  @media (min-width: v.$tablet-width) {
    padding-top: 3px;
    margin-bottom: 0;
    width: unset;
    font-size: 16px;
    line-height: 20px;
  }

  @media (min-width: v.$desktop-width) {
    width: 285px;
  }
}

.add-product__description td {
  padding: 0;

  &:last-child {
    text-align: right;
  }

  @media (min-width: v.$tablet-width) {
    &:last-child {
      padding-left: 49px;
      padding-right: 3px;
    }
  }

  @media (min-width: v.$desktop-width) {
    &:last-child {
      padding-left: 0;
      padding-right: 0;
    }
  }
}

.add-product__button {
  width: 100%;

  @media (min-width: v.$desktop-width) {
    width: 244px;
    margin-right: 1px;
  }
}
